
import axios from 'axios';
import { mapGetters } from 'vuex';
import cookie from 'vux/src/tools/cookie/index.js'


export default {
data(){
return{
userName:'',
dateTime:'',
activeId:'',
activeName:'',
activeMsg:'',
sucStates:'',
userId : cookie.get('userId')
}
},
methods:{
getId(){
this.activeId =this.$route.params.activeId;
this.$vux.alert.show({title:this.$route.params.activeId});
},
getSign(){
this.$http.get('active/approved', {
params: {
userId: this.userId,
activeId:this.$route.params.activeId
}
})
.then(res => {
this.activeMsg = res.msg;
if(res.success == false){
this.sucStates = '签到失败';
}else{
this.sucStates = '恭喜';
}
})
.catch(err => {
console.log(err);
});
},
getActiveName(){
axios({
url:'active/queryById',
method:'post',
headers: {'contentType':'application/x-www-form-urlencode'},
params:{
activeId:this.$route.params.activeId
}
}).then(res => {
this.activeName = res.data.activeName;
}).catch(err => {
console.log(err);
});

},
getUser() {
this.$http.get('ppartymember/queryByUserId', {
params: {
userid: this.userId
}
})
.then(res => {
this.userName = res.data.name;
})
.catch(err => {
console.log(err);
});
},
datePick(s){
Date.prototype.toLocaleString = function(){
return this.getFullYear() +'年'+ (this.getMonth()+1)+'月'+this.getDate()+'日'+this.getHours()+'时'+this.getMinutes()+'分'
}
return new Date(s).toLocaleString();
}
},
components:{
},
mounted(){
this.getUser();
this.getSign();
this.getActiveName();
},
computed: {
...mapGetters(['user'])
},
beforeMount(){
sessionStorage.userRoleId = 4;
}
};



